<template>
	<!--index.wxml-->
	<!-- <PageWrap> -->
	<view class="container">
		<view class="newsListWrap">
			<view v-for="(item, index) in list" :key="index" class="newsListItemWrap">
				<view class="newsListItemWrapNoImg" v-if="!item.imgurl" :data-seq="item.seq" @tap="handleShowArticle">
					<view class="newsListItemTitle">{{item.title}}</view>
					<view class="newsListItemDes">{{item.summ}}</view>
					<view class="newsListItemInfo">
						<view class="newsListItemFrom" v-if="item.source">{{item.source}}</view>
						<view class="newsListItemAgo">{{item.ctime}}</view>
					</view>
				</view>
				<view :class="'newsListItemWrapImg ' + (item.readBefore ? 'newsListItemWrapReadBefore' : '')" v-else :data-seq="item.seq"
				 @tap="handleShowArticle">
					<view class="newsListItemImg">
						<image class="newsListItemImgInner" :src="'https:' + item.imgurl"></image>
					</view>
					<view class="newsListItemInnerWrap">
						<view class="newsListItemTitle">{{item.title}}</view>
						<view class="newsListItemInfo">
							<view class="newsListItemFrom" v-if="item.source">{{item.source}}</view>
							<view class="newsListItemAgo">{{item.ctime}}</view>
						</view>
					</view>
				</view>
			</view>
		</view>
		<view class="newsListLoadMoreWrap">
			<image class="newsListLoadMoreImg" src="../../static/load.png" v-if="!nomore && !reGet"></image>
			<view class="newsListLoadMoreText" v-if="!nomore && !reGet">正在加载更多</view>
			<view class="newsListLoadMoreText" v-if="nomore && !reGet">没有更多了~</view>
			<view class="newsListLoadMoreText" v-if="reGet" @click="reGetData">暂无数据，点击重试</view>
		</view>

		<!-- <Nav default="news"></Nav> -->
	</view>
	<!-- </PageWrap> -->
</template>

<script>
//index.js
//获取应用实例
const utils = require("../../utils/util");
const url = require("../../utils/url");
let curPage = 1;
let reqListUrl = '';
let version = '1.4.5';
// import Nav from "../../components/nav/index";
// import PageWrap from "../../components/pageWrap/index";

export default {
	data() {
		return {
			list: [],
			loading: false,
			nomore: false,
			readBefore: [],
			reGet: false,
			onShareAppMessage: function(options) {
				let that = this;
				// 设置菜单中的转发按钮触发转发事件时的转发内容
				let shareObj = {
					path: `/pages/news/news`
				};
				return shareObj;
			},
			onLoad: function() {
				curPage = 1; // 在顶部三个点中开启转发功能

				wx.showShareMenu(); // 获取浏览过的列表

				let readBefore = wx.getStorageSync('readBefore') || [];
				this.setData({
					readBefore: readBefore
				}); // 获取首页数据
				// this.methods.getData.call(this, curPage)

				this.methods.getReqUrl.call(this, version);
			},
			methods: {
				// 获取接口
				getReqUrl: function(version) {
					utils._get({
						url: url.getReqUrl(version)
					}).then(({
						data
					}) => {
						if (data.headline) {
							reqListUrl = data.headline;
							this.methods.getData.call(this, curPage);
						}
					}).catch(e => {
						console.error(e);
						this.reGet = true;
					});
				},
				// 获取数据
				getData: function(page, fn) {
					if (this.nomore) return;
					this.setData({
						loading: true
					});

					utils._get({
						url: url.newsList(page, reqListUrl)
					}).then(data => {
						if (data.data && !data.data.more) {
							this.setData({
								nomore: true
							});
						}

						if (data.status_code === 0) {
							this.loading = false;

							if (page === 1) {
								this.setData({
									loading: false,
									list: this.methods.formatData.call(this, data.data.list)
								});
							} else {
								this.setData({
									loading: false,
									list: this.list.concat(this.methods.formatData.call(this, data.data.list))
								});
							}

							curPage++;
						} else {
							this.setData({
								loading: false,
								nomore: true
							});
						}

						if (fn) fn();
					}).catch(e => {
						console.log('获取数据失败了')
						this.setData({
							loading: false,
							nomore: true
						});
						console.error(e);
					});
				},
				// 格式化数据
				formatData: function(arr) {
					let newList = [];
					arr.map(item => {
						newList.push({ ...item,
							readBefore: this.readBefore.indexOf(item.seq) >= 0,
							ctime: utils._timeAgo(item.ctime * 1000)
						});
					});
					return newList;
				}
			}
		};
	},

	// components: {
	//   Nav,
	//   PageWrap
	// },
	props: {},
	onPullDownRefresh: function() {
		if (this.loading) return;
		this.setData({
			nomore: false
		});
		curPage = 1;
		this.methods.getData.call(this, curPage, () => {
			wx.stopPullDownRefresh();
		});
	},
	onReachBottom: function() {
		if (this.loading) return;
		this.methods.getData.call(this, curPage);
	},
	onLoad: function() {
		curPage = 1;
		wx.showShareMenu();
		let readBefore = wx.getStorageSync('readBefore') || [];
		this.setData({
			readBefore: readBefore
		});
		this.methods.getReqUrl.call(this, version);
	},
	methods: {
		// 点开文章
		reGetData: function() {
			this.reGet = false;
			setTimeout(() => {
				this.methods.getReqUrl.call(this, version);
			}, 1000)
		},
		handleShowArticle: function(e) {
			let {
				seq
			} = e.currentTarget.dataset;

			if (seq) {
				uni.navigateTo({
					url: `/pages/article/article?seq=${seq}`
				}); // 添加选中的文章seq到本地存储

				let readBefore = this.readBefore || [];
				readBefore.unshift(seq); // 更新已点过的文章的样式

				let newList = this.list;
				newList.map(item => {
					if (item.seq === seq) {
						item.readBefore = true;
					}
				});
				this.setData({
					readBefore: readBefore,
					list: newList
				});
				wx.setStorageSync('readBefore', readBefore);
			}
		}
	}
};
</script>
<style>
	@import "./index.css";
</style>
